import React from 'react';
import Tweet from './Tweet.js';
import $ from 'jquery';

export default React.createClass({
    displayName: 'Content',

    getInitialState: function () {
        return {
            tweets: []
        }
    },

    componentDidMount: function () {
        if(navigator)
        $.ajax({
            url:"http://localhost:4000/tweets.json",
            dataType:"json",
            crossDomain: true,
            success:(json) => {
                console.log(json.statuses);
                this.setState({tweets: json.statuses})
            },
            error:function(){
                alert("Error");
            }
        });
    },

    render: function () {
        return (
            <div>
                <p>Here is some Content { this.state.data }</p>
                {this.state.tweets.map((tweet) => {
                    return (<Tweet key={tweet.id} text={tweet.text} user={tweet.user.name}/>);
                })}
                <input ref='tweetButton' type='button' onClick={ this.getTweet } value='Get new Tweets'></input>
            </div>
        );
    }
});






